<template>
  <div class="loginWrapper">
    <div class="loginContent">
      <div class="loginTitle">
        <img
          :src="require('assets/img/login-left.png')"
          alt="登录标题"
        >
        <span class="loginTitleText">
          登录
        </span>
        <img
          :src="require('assets/img/login-right.png')"
          alt="登录标题"
        >
      </div>
      <div class="loginInput">
        <el-form
          :model="ruleForm2"
          status-icon
          :rules="rules2"
          ref="ruleForm2"
        >
          <el-form-item
            prop="name"
          >
            <el-input
              v-model="ruleForm2.name"
              class="inputWidth"
              placeholder="请输入手机号"
              autocomplete="off"
            />
          </el-form-item>
          <el-form-item
            prop="pass"
          >
            <el-input
              type="password"
              v-model="ruleForm2.pass"
              class="inputWidth"
              placeholder="请输入密码"
              @keyup.enter.native="submitForm('ruleForm2')"
              autocomplete="off"
            />
          </el-form-item>
          <el-form-item>
            <el-button
              class="submit"
              type="primary"
              @click="submitForm('ruleForm2')"
            >
              登录
            </el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="accountWrapper">
        <router-link
          class="accountContent"
          to="/reg"
        >
          没有账号?立即注册
        </router-link>
        <router-link
          class="accountContent"
          to="/retrieve"
        >
          忘记密码?立即重置
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      }
      callback();
    };
    const validateName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入手机号'));
      }
      callback();
    };
    return {
      ruleForm2: {
        pass: '',
        name: '',
      },
      rules2: {
        pass: [
          { validator: validatePass, trigger: 'blur' },
        ],
        name: [
          { validator: validateName, trigger: 'blur' },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      // eslint-disable-next-line consistent-return
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http({
            methods: 'POST',
            url: '/pjsys/login.action',
            params: {
              mobilephone: this.ruleForm2.name,
              password: this.ruleForm2.pass,
            },
          })
            .then((res) => {
              // eslint-disable-next-line no-console
              if (res.data === 0) {
                this.$message({
                  message: '账户或密码错误',
                  type: 'error',
                });
              } else if (res.data !== 0) {
                this.$message({
                  message: '登录成功',
                  type: 'success',
                });
                this.$Storage.set('userId', res.data);
                this.$Storage.set('userTel', this.ruleForm2.name);
                this.$router.push('/main/newnotice');
              }
            });
        } else {
          return false;
        }
      });
    },
  },
};
</script>

